  height: 100vh;
}
.el-header {
  height: 53px;
  background-color: #b3c0d1;
  color: #333;
  /* text-align: center; */
  line-height: 60px;
}

.el-header-div {
  float: right;
}

.el-aside {
  height: 100vh;
  background-color: #20222a;
  color: #333;
  /* text-align: center; */
  line-height: 200px;
  overflow-x: hidden;
}

.el-main {
  background-color: #e9eef3;
  color: #333;
  /* text-align: center;
  line-height: 160px; */
}
.el-menu-vertical-demo {
  text-align: left;
  width: 150px;
  overflow-x: hidden;
  
}
